	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
                tokenSrc:''
			};
		},
		created:function () {
                let _this = this
                // 获取token
                var dataStr = JSON.stringify({"source":"YSG","otId":"1947364"});
                dataStr = 'dataMap=' + dataStr;
                $.ajax({
                    type:'post',
                    url:'https://bird.ioliu.cn/v1/?url=http://www.e-fp.cn/issp/ApiService?method=xforceplus.sys.qToken',
                    async:false,
                    data:dataStr,
                    dataType:'jsonp',
                    success:function(json){
                        console.log('data—>',json.token);
                        _this.tokenSrc = json.token;
                        // //获取发票抬头
                        // if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
                        //     openFile(_this.tokenSrc)
                        // }else{
                        //     openPdf(_this.tokenSrc)
                        // }

                    },
                    error:function () {
                    }
                });
		},
        mounted:function () {
            //一级页面falg
            isHomePage(0)
        },
        methods: {
            showHeader(){
                this.$router.push({path:'/invoicesHeader',query:{token:this.tokenSrc}})
            },
            showManager(){
                this.$router.push({path:'/invoicesManage',query:{token:this.tokenSrc}})
            },

        }
	};



